body {
  background-color: white;
}

.center {
  display: grid;
  place-items: center;
  min-height: 100vh;
  margin: 0;
}

.flex {
  display: flex;
  gap: 2rem;
}

html {
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
}

body {
  background-color: powderblue;
}

.camera {
  position: relative;
  width: 800px;
  height: 560px;
  background-image: linear-gradient(white 0%, white 25%, #ff3c3c 25%, #ff3c3c 75%, white 75%, white 100%);
  border-radius: 50px;
}
.camera::before {
  content: "";
  position: absolute;
  background-color: #41484b;
  top: calc(50% - 180px);
  left: calc(50% - 180px);
  width: 360px;
  height: 360px;
  border-radius: 50%;
  border: 20px, solid, white;
}
.camera::after {
  content: "";
  position: absolute;
  top: calc(50% - 135px);
  left: calc(50% - 135px);
  width: 270px;
  height: 270px;
  border-radius: 50%;
  background-image: radial-gradient(circle at center, #41484b 0%, #41484b 28%, #647074 28%, #647074 61%, darkgrey 61%, darkgrey 100%);
}

.lens-shdw {
  position: relative;
  background-image: linear-gradient(to bottom right, rgba(0, 0, 0, 0.3) 0%, rgba(0, 0, 0, 0.3) 50%, transparent 50%, transparent 100%);
  top: calc(50% - 135px);
  left: calc(50% - 135px);
  width: 270px;
  height: 270px;
  border-radius: 50%;
  z-index: 10;
  overflow: hidden;
}

.details {
  background-color: transparent;
}
.details::before {
  content: "";
  position: absolute;
  background-color: darkred;
  width: 72px;
  height: 16.8px;
  border-top-left-radius: 12px;
  border-top-right-radius: 12px;
  top: -16.8px;
  left: 72px;
}
.details::after {
  content: "";
  position: absolute;
  background-color: #c7f3ff;
  border: 7.2px, solid, lightgray;
  border-radius: 50%;
  width: 72px;
  aspect-ratio: 1;
  top: 33.6px;
  right: 40px;
}